@media screen and (max-width: 1124px) {
  // header
  .header {
    padding-top: 10px;
    padding-bottom: 20px;
    &__container {
      display: flex;
      justify-content: space-between;
      align-items: center; } }
  .hamburger-button {
    display: block; }
  .nav {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 100;
    width: 100%;
    flex-direction: column;
    align-items: center;
    display: none;
    &__part {
      background: #666666;
      width: 100%;
      border-top: none;
      border-bottom: 1px solid #cecece;
      &:hover, &.active {
        border-top: none;
        border-bottom: 2px solid #ffc06f; } } }
  // biography
  .biography {
    &__container {
      flex-direction: column;
      align-items: center; } } }

@media screen and (max-width: 1000px) {
  // gallery
  .gallery {
    &__album {
      width: 30%; } } }

@media screen and (max-width: 450px) {
  // header
  .logo {
    &__header {
      font-size: 24px;
      line-height: 24px; }
    &__footer {
      display: flex;
      align-items: center; }
    &__footer-text {
      font-size: 14px; }
    &__footer-decor {
      width: 4px;
      height: 4px; } }
  // biography
  .biography {
    &__photo {
      width: 100%; }
    &__person {
      width: 100%; } }
  .biography-text {
    &__title-header {
      font-size: 40px; }
    &__title-footer {
      font-size: 20px; }
    &__main {
      font-size: 16px;
      margin-bottom: 50px; }
    &__cap-letter {
      font-size: 31px;
      color: #f6f5c5;
      &-small {
        font-size: 16px; } } }
  // title
  .title {
    display: flex;
    justify-content: center;
    &__text {
      font-size: 25px;
      letter-spacing: 2px; }
    &__decor {
      width: 12px;
      height: 12px; } }
  // gallery
  .gallery {
    &__albums {
      flex-direction: column; }
    &__album {
      width: 100%;
      margin-bottom: 10px; } } }
